export default {
	name: "mix-load-more",
	props: {
		status: {
			//0加载前，1加载中，2没有更多了
			type: Number,
			default: 0
		},
		is_show_font:{
			type: Boolean,
			default: true
		},
		text: {
			type: Array,
			default () {
				return [
					'上拉显示更多',
					'正在加载...',
					'没有更多数据了'
				];
			}
		}
	},
	methods:{
		click(){
			this.$emit("is_click")
		}
	}
}
